@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap');

/**/
:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-blue: #5364f8;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: green;
  --primary-transparent-white: rgba(255, 255, 255, 0.8);
}

body {
font-family: "Roboto Condensed", sans-serif !important;
background-color: #000 !important;
}

/************************ COLORS ********************************/

.purple{
  background-color: #000000;
}


/************************ NAVBAR *********************************/

nav {
  z-index: 2;
  max-height: 75px;
}

.logo {
  width: 3.75rem;
  margin: 1rem;
  transition: transform .7s ease-in-out;
}
.logo:hover {
 
  width: 3.8rem;
  transform: rotate(360deg);
}

.logo a:hover{
  color: var(--primary-black);
}

.navbar-collapse {
  background-color: var(--primary-black);
}

/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}


.navbar {
  background-color: var(--primary-black);
  border-bottom: 1px solid
}

nav ul li {
  
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: var(--primary-white) !important;
  font-size: 2rem;
  font-weight: 500;
  font-family: 'Shadows Into Light', cursive;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}


@media(max-width: 768px) {
 .navbar-brand {
   width: 7rem;
 }
}

@media(max-width: 996px) {
  .background-overlay .background-overlayTwo .background-overlayThree .background-overlayFour a:hover {
    background: none !important;
  }
}
.navbar-nav a:hover {
  color:var(--primary-light-grey) !important;
  /*background: url(./images/navColorOverlay.png) !important;*/
}
.background-overlay a:hover {
  color: #FFFFFF !important;
  background: linear-gradient(to right, #62BB47, #FDB727);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.background-overlayTwo a:hover {
  color: #FFFFFF !important;
  background: linear-gradient(to right, #FDB727, #F5821F);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.background-overlayThree a:hover {
  color: #FFFFFF !important;
  background: linear-gradient(to right, #F5821F, #983D97);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.background-overlayFour a:hover {
  color: #FFFFFF !important;
  background: linear-gradient(to right, #983D97, #E03A3E);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.background-overlayFive a:hover {
  color: #FFFFFF !important;
  background: linear-gradient(to right, #E03A3E, #0F9DDF);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.background-overlaySix a:hover {
  color: #FFFFFF !important;
  background: linear-gradient(to right, #0F9DDF, #62BB47);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}


.navbar-toggler {
  background-color: #000000 !important;
}

.navbar-toggler a{
  color: var(--primary-white) !important;
}

.navbar-light a{
  color: var(--primary-white)
}

.navMenuBtn{
  display: none !important;
}

/************************ HEADER *********************************/

.header-wraper {
  position: relative;
  width: 100%;
  background-size: cover;
  background-position: center;
  height: 95vh;
  background-color: #000000;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: #ffffff;
}

@media(max-width: 580px) {
  .navbar-toggler {
    margin-bottom: 35px;
  }
}
/*-------- TYPED TEXT ---------------*/
.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;

  }
}

/*-------- HEADER MAIN OFFER BUTTON ---------------*/
.btn-main-offer {
  font-size: 2rem;
  font-weight: 700;
  border: 1px solid var(--primary-white);
  cursor: pointer;
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  margin: 2rem;
  font-family: 'Shadows Into Light', cursive;      
}

.btn-main-offer:hover {
  text-decoration: none;
   
  color: var(--primary-white);
  transition:  .2s ease-in-out;
}

.btn-main-offer a:hover {
  background: linear-gradient( #62BB47, #FDB727, #F5821F, #983D97, #E03A3E, #0F9DDF);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

/*-------- PARTICLES JS ---------------*/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: -1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}



/************************ ABOUT ME *********************************/
.aboutSection {
  background-color: var(--primary-black);
  max-width: 75rem !important;
  margin-top: -15px;
}

.aboutSection p {
  color: var(--primary-white);
}
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 21rem;
  border-radius: 50%;
  padding: .3rem;
  margin-left:8rem;
}

.profile-img:hover {
  background: linear-gradient( #62BB47, #FDB727, #F5821F, #983D97, #E03A3E, #0F9DDF);
}

.about-heading {
  font-family: 'Shadows Into Light', cursive;
  color: var(--primary-white);
  text-transform: uppercase;       
}

.about-heading:hover {
  background: linear-gradient( #62BB47, #FDB727);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;     
}
 .about-header h1, p{
  font-family: 'Shadows Into Light', cursive !important;
}
.about-header p {
  font-size: 1.7rem;
}
.aboutLeft {
  right: 59px;
}
.aboutRight {
  margin-left: -8rem;
  margin-top: 6rem;
}
@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/************************ SERVICES *********************************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: var(--primary-black);
}

.services h1, h3, p {
  color: var(--primary-white);
  text-transform: uppercase;
  font-family:'Shadows Into Light', cursive !important;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: url(./images/navColorOverlay.png) !important;
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid var(--primary-black);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: rgba(255, 255, 255, 0.14);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: rgba(169, 174, 255, 0.911);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
  color: var(--primary-white);
}

.services .box:hover .circle {
  background: var(--primary-white);
}

.services .box:hover p {
  color: var(--primary-white);
}

/************************ EXPERIENCE *********************************/
.workDescriptions {
  margin-left: -15px;
}
.experience {
  padding: 0.7rem;
  background: var(--primary-black);
  
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color:#FFFFFF;
  font-size: 3rem;
  font-weight: 800;
  background-repeat: no-repeat;
  font-family: 'Shadows Into Light', cursive;
}

.experience h1:hover, h3:hover {
  background: linear-gradient( #62BB47, #FDB727, #F5821F, #983D97, #E03A3E, #0F9DDF);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;      
}

.experience h3:hover {
  background: linear-gradient( #E03A3E, #0F9DDF);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;      
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
  left: -7px;
  position: relative;
}

.timeline-block-left {
  float: left;
  direction: rtl;
  right: -7px;
  position: relative;
}

.marker {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;;
  border: .5px solid var(--primary-white);
  background: linear-gradient(#62BB47, #FDB727, #F5821F, #983D97, #E03A3E, #0F9DDF);
  margin-top: 0.625rem;
  z-index: 3;
  transition: transform .7s ease-in-out;
}
.marker:hover {
  transform: rotate(360deg);
}
.markerLeft {
 
}
.markerLeft:hover {
  
}
.markerRight {
  padding-right: 1255px;
}
.markerRight:hover {
 
}
.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: var(--primary-white);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: var(--primary-white);
  font-weight: 900;
  font-family:'Shadows Into Light', cursive !important;
  padding-left: .5rem;
  font-size: 2.5rem;
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
.experience .container {
  padding-left: 0;
}

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  
  .timeline-block-left {
    float: none;
    direction: ltr; 
    width: 100%;
  }
}

/************************ PORTFOLIO *********************************/
#portfolio .row {
  flex-wrap: nowrap !important;
}
.portfolio-wrapper {
  background: var(--primary-black);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: var(--primary-white);
  font-weight: 900;
  font-family:'Shadows Into Light', cursive !important;
  font-size: 3rem;
}

.portfolio-image {
  width: 35rem;
 
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

/*------------ OVERFLOW BOX ------------------*/
.overflow {
  position: absolute;
   opacity: 0;
   background: var(--primary-black);
   width: 35rem;
   height: 32rem;
   top: -8px;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 7rem;
}

/*------------ OVERFLOW BOX ------------------*/
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer; 
  color: var(--primary-orange);
}

.hyper-link:hover {
  color: var(--primary-red);
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {
.popupbox-wrapper {
  height: 100%;
}
  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
    .portfolio-image-popupbox {
      width: 30%;
    }
  }

/************************ TESTIMONIALS *********************************/
.testimonials {
  background-size: cover;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: var(--primary-white);
  padding: 3rem 0;
  font-family: 'Shadows Into Light', cursive;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: var(--primary-white); 
  border-radius: 5px;
}
.carousel .control-dots .dot {
  background: linear-gradient(  #FDB727, #F5821F, #983D97, #E03A3E) !important;
  transform: scale(250%);
      
}

.control-arrow {
  background-color: #f9ab00 !important;
}
.myCarousel {
  color: var(--primary-black);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.myCarousel h3 {
  color: var(--primary-black);
  font-weight: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: var(--primary-black);
}

.carousel .slide img {
  width: 10rem !important;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
}

/************************ CONTACTS COMPONENT *********************************/
.contacts {
  background: var(--primary-black);
  padding: 3rem 0;
}

.contacts h1 {
  color: var(--primary-light-grey);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/*------------------ INPUTS -------------------------------*/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: var(--primary-white);
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: var(--primary-green);
}

/*---------------- MEDIA -----------------------*/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/************************ FOOTER COMPONENT *********************************/
.footer {
  background: var(--primary-black);
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-orange);
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}


/************ Mobile Screen Resizing *************/

@media(max-width: 1200px) {
  a.nav-link {
    font-size: 1.2rem !important;
  }
  .logo {
    position: fixed;
    top: -10px;
  }
}

@media(max-width: 992px) {
  a.nav-link {
    font-size: 1rem !important;
  }
  .logo {
    
  }
  .navMenuBtn{
    display: block !important;
    border: none !important;
    background: none;
    position: absolute;
    right: 55px;
    top: 4px;
    padding-top: 8px;
    transform: scale(0.9);
  }
  
  .navMenuBtn:hover{
    transform: scale(1.0);
  }
  
  
}

@media(max-width: 768px) {
  a.nav-link {
    font-size: 0.5rem !important;
  }
}

















